* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  background-color: #cac7c7;
  padding: 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul, li {
  list-style: none;
}

a {
  color: #fff;
  background-color: #4d79ff;
  margin-bottom: 10px;
  padding: 5px 15px;
  font-size: 1.07em;
  font-weight: 500;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

a:-webkit-any(:hover, :focus) {
  background-color: #285dff;
}

a:is(:hover, :focus) {
  background-color: #285dff;
}

input {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #000;
  outline: none;
  padding: 5px 10px;
  font-size: 1.2em;
}

img {
  max-width: 100%;
  display: block;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #121212;
  padding: 5px 15px;
  font-size: 1.2em;
  transition: background-color .3s;
}

button:-webkit-any(:hover, :focus) {
  background-color: #252525;
}

button:is(:hover, :focus) {
  background-color: #252525;
}

label {
  font-size: 1.1em;
  font-weight: 500;
}

#datetime-picker {
  margin-bottom: 30px;
}

.timer {
  gap: 15px;
  margin-top: 30px;
  display: flex;
}

.field {
  max-width: 115px;
  max-height: 112px;
  background-color: #cac7c7;
  border: 1px solid #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding: 20px 30px;
  display: flex;
}

.value {
  font-size: 36px;
  font-weight: 700;
  line-height: 50px;
}

.label {
  font-weight: 600;
  line-height: 22px;
}

.form {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

form button {
  max-width: 200px;
  width: 100%;
  display: block;
}

